* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-input {
  width: 300px;
  margin-bottom: 10px;
}

.country-list {
  list-style: none;
  padding: 0;
}

.country-list__item {
  display: flex;
  justify-content: start;
  align-items: center;
}

.country-list__item:not(:last-child) {
  margin-bottom: 10px;
}

.country-info__flag {
  width: 30px;
  margin-right: 10px;
}

.country-info__list {
  list-style: none;
  padding: 0;
}

.country-info__item:not(:last-child) {
  margin-bottom: 20px;
}

.country-info__name {
  font-weight: 700;
}
